input:valid {
  width: 250px;
  border-style: solid;
}

input:invalid {
  width: 250px;
  border-style: solid;
  border-color: red;
}

button {
  width: 250px;
  border-style: solid;
  border-color: gray;
}

h1{
  font-family: 'Raleway', sans-serif;
  text-align: center;
  font-size: 500%;
  font-weight: bold;
}

h2, h3, h4{
  font-family: 'Raleway', sans-serif;
}

.smallPadding{
  padding: 10px;
}

.bigPadding{
  padding-left: 50px;
}

.outputBox{
  display: grid;
  grid-template-columns: 100px 20px auto;
}

.coffeeMugName{
  grid-column: 1/2;
  display: inline;
  width: 200px;
}

.coffeeMug{
  grid-column: 3/4;
  display: inline;
}

.outputBoxEdit{
  display: grid;
  grid-template-columns: 300px 100px 300px;
  padding-left: 50px;
}

.deleting{
  grid-column: 1/2;
  display: inline;
}

.editing{
  grid-column: 3/4;
  display: inline;
}

.react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
  border-bottom: 1px solid #aaa;
  margin: 0 60px 10px;
  padding: 0;
}

.react-tabs__tab {
  display: inline-block;
  border: 1px solid transparent;
  border-bottom: none;
  bottom: -1px;
  position: relative;
  list-style: none;
  padding: 6px 22px;
  cursor: pointer;
}

.react-tabs__tab--selected {
  background: #fff;
  border-color: #aaa;
  color: black;
  border-radius: 5px 5px 0 0;
}

.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
}

.react-tabs__tab:focus {
  box-shadow: 0 0 5px hsl(208, 99%, 50%);
  border-color: hsl(208, 99%, 50%);
  outline: none;
}

.react-tabs__tab:focus:after {
  content: "";
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: #fff;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}

.error { border: 1px solid red; }
